.health-page{font-family:"Segoe UI",sans-serif;color:#2e2e2e;background-color:#fefbf6}.health-page .banner{position:relative;width:100%;height:400px;overflow:hidden;border-bottom:5px solid #34a853}.health-page .banner img{width:100%;height:100%;object-fit:cover;filter:brightness(0.55);transition:transform .5s ease}.health-page .banner:hover img{transform:scale(1.05)}.health-page .banner .overlay{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);color:#fff;text-align:center;backdrop-filter:blur(2px)}.health-page .banner .overlay h1{font-size:3.5rem;font-weight:bold;margin-bottom:.5rem;letter-spacing:1px;text-shadow:0 2px 5px rgba(0,0,0,.3)}.health-page .banner .overlay p{font-size:1.4rem;font-weight:300;opacity:.95}.health-page .content{max-width:1000px;margin:60px auto;padding:0 25px}.health-page .content h2{font-size:2rem;margin-top:40px;color:#34a853;position:relative}.health-page .content h2::after{content:"";display:block;width:60px;height:3px;background-color:#34a853;margin-top:8px;border-radius:3px}.health-page .content p{font-size:1.15rem;line-height:1.8;margin-top:15px;color:#3c3c3c}.health-page .content ul{margin-top:25px;padding-left:25px;display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));gap:15px;list-style:none}.health-page .content ul li{background-color:#fff;padding:15px 20px;border-radius:12px;box-shadow:0 4px 14px rgba(52,168,83,.1);font-size:1.1rem;position:relative;transition:all .3s ease;cursor:pointer}.health-page .content ul li::before{content:"🌿";position:absolute;left:-25px}.health-page .content ul li:hover{background-color:#e6f6ea;transform:translateY(-3px)}@media(max-width: 768px){.health-page .banner{height:280px}.health-page .banner .overlay h1{font-size:2.2rem}.health-page .banner .overlay p{font-size:1rem}.health-page .content{padding:0 15px}.health-page .content h2{font-size:1.5rem}.health-page .content p,.health-page .content li{font-size:1rem}}