.food-page{font-family:"Segoe UI",sans-serif;color:#2e2e2e;background-color:#fdfdfb}.food-page .banner{position:relative;width:100%;height:400px;overflow:hidden}.food-page .banner img{width:100%;height:100%;object-fit:cover;filter:brightness(0.55);transition:transform .5s ease}.food-page .banner:hover img{transform:scale(1.03)}.food-page .banner .overlay{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;color:#fff;backdrop-filter:blur(3px)}.food-page .banner .overlay h1{font-size:3.5rem;font-weight:700;text-shadow:0 3px 10px rgba(0,0,0,.4);margin-bottom:10px}.food-page .banner .overlay p{font-size:1.4rem;font-weight:300;max-width:700px;margin:0 auto;opacity:.95}.food-page .content{max-width:1000px;margin:60px auto;padding:0 25px}.food-page .content h2{font-size:2rem;color:#27ae60;margin-top:40px;position:relative}.food-page .content h2::after{content:"";display:block;width:60px;height:4px;background-color:#27ae60;margin-top:8px;border-radius:4px}.food-page .content p{font-size:1.15rem;line-height:1.75;margin-top:15px;color:#3c3c3c}.food-page .content ul{margin-top:30px;display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:20px;list-style:none;padding:0}.food-page .content ul li{background-color:#eafff2;border-left:6px solid #27ae60;padding:20px;border-radius:12px;box-shadow:0 4px 14px rgba(39,174,96,.15);font-size:1.1rem;transition:all .3s ease;position:relative;cursor:pointer}.food-page .content ul li:hover{background-color:#d9f8e6;transform:translateY(-4px)}.food-page .content ul li::before{content:"🥗";margin-right:10px}@media(max-width: 768px){.food-page .banner{height:280px}.food-page .banner .overlay h1{font-size:2.4rem}.food-page .banner .overlay p{font-size:1rem;padding:0 10px}.food-page .content{padding:0 15px}.food-page .content h2{font-size:1.6rem}.food-page .content p,.food-page .content li{font-size:1rem}}