.advice-page{font-family:"Segoe UI",sans-serif;color:#2e2e2e;background-color:#fdfaff}.advice-page .banner{position:relative;width:100%;height:400px;overflow:hidden}.advice-page .banner img{width:100%;height:100%;object-fit:cover;filter:brightness(0.55);transition:transform .5s ease}.advice-page .banner:hover img{transform:scale(1.03)}.advice-page .banner .overlay{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;color:#fff;backdrop-filter:blur(3px)}.advice-page .banner .overlay h1{font-size:3.5rem;font-weight:700;text-shadow:0 3px 10px rgba(0,0,0,.4);margin-bottom:10px}.advice-page .banner .overlay p{font-size:1.4rem;font-weight:300;max-width:700px;margin:0 auto;opacity:.95}.advice-page .content{max-width:1000px;margin:60px auto;padding:0 25px}.advice-page .content h2{font-size:2rem;color:#8e44ad;margin-top:40px;position:relative}.advice-page .content h2::after{content:"";display:block;width:60px;height:4px;background-color:#8e44ad;margin-top:8px;border-radius:4px}.advice-page .content p{font-size:1.15rem;line-height:1.75;margin-top:15px;color:#3c3c3c}.advice-page .content ul{margin-top:30px;display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:20px;list-style:none;padding:0}.advice-page .content ul li{background-color:#f3e6fa;border-left:6px solid #8e44ad;padding:20px;border-radius:12px;box-shadow:0 4px 14px rgba(142,68,173,.15);font-size:1.1rem;transition:all .3s ease}.advice-page .content ul li:hover{background-color:#ebd4f7;transform:translateY(-4px)}.advice-page .content ul li::before{content:"💬";margin-right:10px}@media(max-width: 768px){.advice-page .banner{height:280px}.advice-page .banner .overlay h1{font-size:2.4rem}.advice-page .banner .overlay p{font-size:1rem;padding:0 10px}.advice-page .content{padding:0 15px}.advice-page .content h2{font-size:1.6rem}.advice-page .content p,.advice-page .content li{font-size:1rem}}