.logistics-page{font-family:"Segoe UI",sans-serif;color:#2e2e2e;background-color:#f9fbff}.logistics-page .banner{position:relative;width:100%;height:400px;overflow:hidden}.logistics-page .banner img{width:100%;height:100%;object-fit:cover;filter:brightness(0.55);transition:transform .5s ease}.logistics-page .banner:hover img{transform:scale(1.03)}.logistics-page .banner .overlay{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;color:#fff;backdrop-filter:blur(3px)}.logistics-page .banner .overlay h1{font-size:3.5rem;font-weight:700;text-shadow:0 3px 10px rgba(0,0,0,.4);margin-bottom:10px}.logistics-page .banner .overlay p{font-size:1.4rem;font-weight:300;max-width:700px;margin:0 auto;opacity:.95}.logistics-page .content{max-width:1000px;margin:60px auto;padding:0 25px}.logistics-page .content h2{font-size:2rem;color:#2980b9;margin-top:40px;position:relative}.logistics-page .content h2::after{content:"";display:block;width:60px;height:4px;background-color:#2980b9;margin-top:8px;border-radius:4px}.logistics-page .content p{font-size:1.15rem;line-height:1.75;margin-top:15px;color:#3c3c3c}.logistics-page .content ul{margin-top:30px;display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));gap:20px;list-style:none;padding:0}.logistics-page .content ul li{background-color:#e8f1ff;border-left:6px solid #2980b9;padding:20px;border-radius:12px;box-shadow:0 4px 14px rgba(52,152,219,.15);font-size:1.1rem;transition:all .3s ease;position:relative;cursor:pointer}.logistics-page .content ul li:hover{background-color:#d7eaff;transform:translateY(-4px)}.logistics-page .content ul li::before{content:"🚛";margin-right:10px}@media(max-width: 768px){.logistics-page .banner{height:280px}.logistics-page .banner .overlay h1{font-size:2.4rem}.logistics-page .banner .overlay p{font-size:1rem;padding:0 10px}.logistics-page .content{padding:0 15px}.logistics-page .content h2{font-size:1.6rem}.logistics-page .content p,.logistics-page .content li{font-size:1rem}}